// export default OrderSummary;
import React, { useState, useEffect } from "react";
import { fetchUserAddresses, addAddress } from "../service/UserService";

const OrderSummary = ({ token, userId, orders, totalBill }) => {
  const [addresses, setAddresses] = useState([]);
  const [selectedAddress, setSelectedAddress] = useState(null);
  const [showForm, setShowForm] = useState(false);
  const [newAddress, setNewAddress] = useState({
    street: "",
    city: "",
    state: "",
    country: "",
    zip: "",
    isPrimary: false,
  });
  const [phoneNumber, setPhoneNumber] = useState("");
  const [phoneError, setPhoneError] = useState("");
  const [loading, setLoading] = useState(false);

  // Fetch user addresses on load
  useEffect(() => {
    loadAddresses();
  }, []);

  const loadAddresses = async () => {
    setLoading(true);
    const result = await fetchUserAddresses(token, userId);

    if (result.status === "success") {
      setAddresses(result.data);
      const primaryAddress = result.data.find((address) => address.isPrimary);
      if (primaryAddress) setSelectedAddress(primaryAddress);
    } else {
      console.error(result?.message || "Error retrieving address");
    }
    setLoading(false);
  };

  const handleAddAddress = async () => {
    try {
      const result = await addAddress(token, userId, newAddress);
      if (result.status === "success") {
        loadAddresses();
        setShowForm(false);
        setNewAddress({
          street: "",
          city: "",
          state: "",
          country: "",
          zip: "",
          isPrimary: false,
        });
      } else {
        console.error(result?.message || "Error adding address");
      }
    } catch (error) {
      console.error(error);
    }
  };

  const validatePhoneNumber = () => {
    if (!/^\d{10}$/.test(phoneNumber)) {
      setPhoneError("Please enter a valid 10-digit phone number.");
      return false;
    }
    setPhoneError("");
    return true;
  };

  const handlePhoneNumberChange = (e) => {
    setPhoneNumber(e.target.value);
    setPhoneError("");
  };

  const handleAddressSelection = (address) => {
    setSelectedAddress(address);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewAddress((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className="bg-gray-100">
      <div className="p-6 shadow-md bg-white">
        <h1 className="text-xl font-semibold">Order Summary</h1>

        {/* Phone Number Section */}
        <div className="mt-4">
          <label className="block text-sm font-medium">Phone Number</label>
          <input
            type="text"
            className={`w-full p-2 border rounded ${
              phoneError ? "border-red-500" : "border-gray-300"
            }`}
            placeholder="Enter your phone number"
            value={phoneNumber}
            onChange={handlePhoneNumberChange}
            onBlur={validatePhoneNumber}
          />
          {phoneError && <p className="text-red-500 text-sm mt-1">{phoneError}</p>}
        </div>

        {/* Address Section */}
        <div className="mt-6">
          <h2 className="text-lg font-medium">Select Delivery Address</h2>
          {addresses.map((address) => (
            <div
              key={address.id}
              className={`p-4 border rounded mt-2 ${
                selectedAddress?.id === address.id ? "border-blue-500" : "border-gray-300"
              }`}
              onClick={() => handleAddressSelection(address)}
            >
              <p>{address.street}, {address.city}, {address.state}</p>
              <p>{address.country} - {address.zip}</p>
              {address.isPrimary && <p className="text-green-500 text-sm">Primary Address</p>}
            </div>
          ))}
          <button
            className="mt-4 text-blue-500 underline"
            onClick={() => setShowForm(!showForm)}
          >
            {showForm ? "Cancel" : "Add New Address"}
          </button>

          {/* Add Address Form */}
          {showForm && (
            <div className="mt-4 p-4 border rounded">
              {Object.keys(newAddress).map((field) => (
                <div key={field} className="mb-3">
                  <label className="block text-sm font-medium capitalize" htmlFor={field}>
                    {field.replace(/([A-Z])/g, " $1")}
                  </label>
                  <input
                    type="text"
                    id={field}
                    name={field}
                    className="w-full p-2 border rounded"
                    value={newAddress[field]}
                    onChange={handleInputChange}
                  />
                </div>
              ))}
              <button
                className="w-full bg-blue-500 text-white p-2 rounded"
                onClick={handleAddAddress}
              >
                Add Address
              </button>
            </div>
          )}
        </div>

        {/* Order Summary Section */}
        <div className="mt-6">
          <h2 className="text-lg font-medium">Order Summary</h2>
          <div className={`${!selectedAddress || phoneError ? "opacity-50" : ""}`}>
            {orders.map((item, index) => (
              <div key={index} className="flex items-center mb-4">
                <img
                  className="w-20 h-20 object-cover rounded border"
                  src={item.product?.image || "/_assets/image.png"}
                  alt={item.product?.name}
                />
                <div className="ml-4">
                  <h3 className="text-sm font-medium">{item.product?.name}</h3>
                  <p className="text-sm text-gray-500">{item.product?.description}</p>
                  <p className="text-sm font-bold">${item.product?.offerPrice}</p>
                </div>
              </div>
            ))}
            <p className="font-medium text-lg">Total: ${totalBill}</p>
          </div>
          <button
            className="w-full mt-4 p-3 bg-gray-900 text-white rounded hover:bg-gray-800"
            disabled={!selectedAddress || phoneError}
          >
            Proceed to Payment
          </button>
        </div>
      </div>
    </div>
  );
};

export default OrderSummary;
